@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);

.navbar{
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    background:rgba(32, 31, 31, 0.7);
    height: 9rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s ease-in-out;
    padding-left: 11rem;
}
.navbar ul > a{
    color:#00C9C8;
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: bold;
    margin: 2rem 3rem;
    padding:.6rem 1rem;
    border-radius: .5rem;
    border:.1rem solid #fff;
    transition: 0.4s ease-in-out;
}
.navbar .logo{
    position: absolute;
    left:2rem;
    width: 7rem;
    top: 2rem;
}

.navbar ul a:last-child{
    margin-left: 8rem;
   background:#00C9C8;
   color: #fff;
}
.navbar a img{
    width: 7rem;
}

.navbar ul a:hover ,
.navbar ul a.active {
  box-shadow: inset 0 0 0 2rem #fff;
  color:#00C9C8
}

.mobile__menu{
    padding: .5rem 1.3rem;
    font-size: 3rem;
    border: .1rem solid #fff;
    position: absolute;
    right: 2rem;
    top: 2rem;
    display: none;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.8s ease-in-out;
}
.fa-times{
    padding:0.5rem .3rem;
}

@media screen and (max-width:650px){
    .mobile__menu{
        display: block;
    }
    .nav{
        display: none;
    }
    
   .responsive{
       height: 90vh;
       transition: 0.6s ease-in-out;
   }
   .responsive ul{
       display: flex;
       flex-direction: column;
       position:absolute;
       top: 5rem;
       transition: 0.8s ease-in-out;
   }
   .responsive ul > a{
       width:40rem;
       font-size: 2rem;
       margin:10px;
       text-align: center;
    transition: 0.8s ease-in-out;
   }
   .navbar ul a:last-child{
       margin-left: 0;
       text-align: center;
}
  
}

