  .name {
      display: inline;
    background-image: radial-gradient(
      ellipse 50% 35% at 50% 50%,
      #3bdad2,
      transparent
    );
    text-transform: uppercase;
    font-size: 4rem;
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #00C9C8,
      -0.2rem 0.1rem 1rem #00C9C8, 0.2rem 0.1rem 1rem #00C9C8,
      0 -0.5rem 2rem #00C9C2, 0 0.5rem 3rem #00C9C2;
    -webkit-animation: shine 2s forwards, flicker 3s infinite;
            animation: shine 2s forwards, flicker 3s infinite;
  }
  
  @-webkit-keyframes blink {
    0%,
    22%,
    36%,
    75% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #00C9C8,
        -0.2rem 0.1rem 1rem #00C9C8, 0.2rem 0.1rem 1rem #00C9C8,
        0 -0.5rem 2rem #00C9C2, 0 0.5rem 3rem #00C9C2;
    }
    28%,
    33% {
      color: #00C9C8;
      text-shadow: none;
    }
    82%,
    97% {
      color: #00C9C2;
      text-shadow: none;
    }
  }
  
  @keyframes blink {
    0%,
    22%,
    36%,
    75% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #00C9C8,
        -0.2rem 0.1rem 1rem #00C9C8, 0.2rem 0.1rem 1rem #00C9C8,
        0 -0.5rem 2rem #00C9C2, 0 0.5rem 3rem #00C9C2;
    }
    28%,
    33% {
      color: #00C9C8;
      text-shadow: none;
    }
    82%,
    97% {
      color: #00C9C2;
      text-shadow: none;
    }
  }
  
  .flicker {
    -webkit-animation: shine 2s forwards, blink 3s 2s infinite;
            animation: shine 2s forwards, blink 3s 2s infinite;
    margin-left: 2rem;
  }
  
  .fast-flicker {
    -webkit-animation: shine 2s forwards, blink 10s 1s infinite;
            animation: shine 2s forwards, blink 10s 1s infinite;
    margin-left: 2rem;
  }
  
  @-webkit-keyframes shine {
    0% {
      color: #10C9C2;
      text-shadow: none;
