.about{
    background-image:linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.2)),url(https://image.freepik.com/free-photo/narrow-muddy-road-dark-forest_181624-9675.jpg);
    text-align: center;
}

.about h1{
    color:#00C9C8
}
.about__me {
    position: relative;
    display: flex;
    justify-content: center;
}
.about__img{
    position: absolute;
    width: 85%;
}
.about__text{
    position: absolute;
    top: 15rem;
    text-align: center;
    font-size: 2.4rem;
    padding: 0 16rem;
    text-align: justify;
}

.skills{
    background-image:linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.2)),url(https://image.freepik.com/free-vector/neon-light-arrow-direction-perspective_1017-22033.jpg);
    text-align: center;
}
.progress__bar{
    display: flex;
    flex-direction: column;
    justify-content:space-around;
    align-items: center;
    margin-top: 5rem;
}

.progress__bar label{
    text-align: left;
    font-size: 2rem;
    margin-top: 2rem;
}
.projects{
    background-image:linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.2)),url(https://image.freepik.com/free-photo/dramatic-dark-cloud-background_1421-539.jpg);
    box-sizing: border-box;
    text-align: center;
}
.projects h1{
    margin-bottom: 2rem;

}
button{
    color:#06dfdf;
    margin:1rem;
    padding:.5rem 1rem;
    border-radius: .5rem;
    outline: none;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}
button.active,button:hover{
    background:#f6546a;
}
.project__container img{
    width:33rem;
    height:17rem;
}
.project__container{
    padding:2rem;
    text-align: center;
}
#project{
    width: 30%;
    float: left;
    margin-right: 3rem;
    margin-bottom: 2rem;
    background: rgba(26, 23, 23, 0.453);
    overflow: hidden;
    border-radius: 1rem;
    box-shadow: 0 1rem 2rem #000;
}
#project > h3 {
    color: #00c9c8;
    font-size: 2rem;
}
.links{
    display: flex;
    justify-content: space-around;
    padding: .3rem 0;
    font-size: 1.4rem;
}
.links a:first-child{
    color:red
}
.links a:last-child{
    color: green
}
#project p{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 1rem;
}
@media screen and (max-width:700px){
    #project{
        width:45%
    }
    .projects{
        min-height: 150rem;
}
}
@media screen and (max-width:500px){
    #project{
        width:70%;
        margin-left: 8rem;
    }
    .projects{
        min-height: 270rem;
}
}
.contact{
    background-image:linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.2)),url(https://image.freepik.com/free-photo/digital-world-map-hologram-blue-background_1379-900.jpg);
    text-align: center;
    display: flex;
    padding: 4rem;
    align-items: center;
    justify-content: center;
    padding: 4rem;
}

  .form{
    display: flex;
    flex: 50% 1;
    flex-direction: column;
    background:rgba(0, 0, 0, 0.726);
    opacity:0.9;
    padding:1rem 2rem;
    border-radius: 1rem;
  }
  .form>label{
    margin:.5rem 0 .5rem 1rem;
    font-weight: bold;
    color: yellow;
    letter-spacing: .3rem;
  }
  .form input{
    color:black;
    font-weight: bold;
    font-family: sans-serif;
    font-size: 2rem;
    outline:rgb(216, 197, 197);
    border:.2rem solid rgb(216, 204, 204);
    border-radius: .5rem;
    padding:.5rem 1rem;
  }
  .form h2{
      padding:3rem;
  }
  .form textarea{
    color:black;
    font-weight: bold;
    font-family: sans-serif;
    font-size: 2rem;
    outline:rebeccapurple;
    border:2px solid rgb(189, 183, 183);
    border-radius: .5rem;
    padding:.5rem 1rem;
    height: 10rem;
  }
  .form > button {
    background-color:rgb(54, 141, 212);
    margin-top:2rem;
    padding:.5rem 2rem;
    font-size: 2.5rem;
    font-weight: bold;
    color:white;
    border-radius: .5rem;
    outline: none;
    cursor: pointer;
  }
  .form >button:hover{
    background-color: rgb(94, 141, 212);
  }
  .error {
    color:green
  }
  
  .map{
    width: 100%;
  }
  .form{
      width: 70%;
  }
  .fa-map-marker-alt{
    color:red;
    font-size:3rem
  }
  @media screen and (max-width:666px){
    .contact > .overlay{
        flex-direction: column;
    }
    .contact >.overlay,.contact{
        min-height: 130rem;
    }
  }
.social a {
  position: fixed;
  text-decoration: none;
  color: #fff;
  background: rgb(26, 23, 23);
  padding: 1rem;
  border-radius: 0.5rem 0 0 0.5rem;
  right: -12rem;
  transition: 0.5s;
  font-size: 1.5rem;
}
.social a > i {
  margin-right: 1rem;
}
.social a:hover {
  right: 0rem;
  transform: scale(1.2);
}
.social .facebook {
  bottom: 11rem;
  right: -9.8rem;
}
.social .github {
  bottom: 6rem;
  right: -7.4rem;
}
.social .linkedin {
  bottom: 1rem;
  right: -9rem;
}

